:root {
  --main-bg-color: #fff;
  --main-text-color: #2e2f42;
  --main-nav-color: #4d5ae5;
  --main-hover-color: #404bbf;
  --main-p-color: #434455;
  --main-p-bottom-color: #f4f4fd;
  --top-line-color: #e7e9fc;
  --icon-hover-green: #31d0aa;
  --customers-border: #8e8f99;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
  --input-color: rgba(46, 47, 66, .2);
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

img {
  display: block;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body {
  color: var(--main-text-color);
  background-color: #4693db;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  font-size: 16px;
}

.container {
  max-width: 428px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media only screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media only screen and (min-width: 1200px) {
  .container {
    max-width: 1158px;
  }
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.section-title {
  text-align: center;
  padding-top: 90px;
  padding-bottom: 90px;
}

.header {
  z-index: 100;
  width: 100%;
  border-bottom: 1px solid var(--top-line-color);
  box-shadow: #333;
  background-color: #edf0f2;
  position: fixed;
  top: 0;
  left: 0;
}

.menu-nav {
  display: flex;
}

.logo {
  margin-right: 76px;
  padding: 12px 0;
  text-decoration: none;
}

.video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -100;
  background-position: 50%;
  background-size: cover;
  margin: auto;
  position: absolute;
  top: -100%;
  bottom: -100%;
  left: -100%;
  right: -100%;
}

.hero-text {
  color: #fff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
  font-size: 24px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.about {
  color: #fff;
  margin-top: 100vh;
}

.about-photos {
  width: 100%;
  background-color: #edf0f2;
}

.footer {
  color: #060940;
  background-color: #e69257;
}

.grid-container {
  grid-template-columns: repeat(3, 1fr);
  display: grid;
}

.item {
  padding: 10px;
}

.select-menu {
  max-width: 330px;
}

.select-menu .select-btn {
  cursor: pointer;
  border-radius: 8px;
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
  font-weight: 400;
  display: flex;
}

.select-menu .options {
  width: 330px;
  max-height: 295px;
  background: #fff;
  border-radius: 8px;
  margin-top: 10px;
  padding: 10px;
  animation-name: fadeInDown;
  animation-duration: .35s;
  animation-fill-mode: both;
  position: absolute;
  overflow-y: auto;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.select-menu .options .option {
  height: 55px;
  cursor: pointer;
  background: #fff;
  border-radius: 8px;
  align-items: center;
  padding: 0 16px;
  display: flex;
}

.select-menu .options .option:hover {
  background: #f2f2f2;
}

.select-menu .options .option i {
  margin-right: 12px;
  font-size: 25px;
}

.select-menu .options .option .option-text {
  color: #333;
  font-size: 18px;
}

.select-btn i {
  font-size: 25px;
  transition: all .3s;
}

.select-menu.active .select-btn i {
  transform: rotate(-180deg);
}

.select-menu.active .options {
  opacity: 0;
  z-index: 10;
  animation-name: fadeInUp;
  animation-duration: .4s;
  animation-fill-mode: both;
  display: block;
}

@keyframes fadeInUp {
  from {
    transform: translate3d(0, 30px, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeInDown {
  from {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }

  to {
    opacity: 0;
    transform: translate3d(0, 20px, 0);
  }
}



